"use strict";

// Testimonials data
var testimonials = [{
  id: 1,
  name: 'Sergejs',
  img: './img/testimonials/person1.jpg',
  text: ' -   .        .  - .      ,   , .))'
}, {
  id: 2,
  name: 'Ineta',
  img: './img/testimonials/person2.jpg',
  text: 'oti profesionla, patkama, relaksjo masa. Oksana ir brnigs cilvks ar zeltam rokm. Noteikti iesku!'
}, {
  id: 3,
  name: 'Peter',
  img: './img/testimonials/person3.jpg',
  text: '  ,      ,    ,     . , )'
}];

// Items
var img = document.getElementById('person-img');
var firstName = document.getElementById('firstName');
var review = document.getElementById('review');

// Buttons
var prevBtn = document.querySelector('.prev-btn');
var nextBtn = document.querySelector('.next-btn');

// Set starting Item
var currentItem = 0;

// Get current Item
window.addEventListener('DOMContentLoaded', function () {
  showPerson(currentItem);
});

// Show Person
function showPerson() {
  var item = testimonials[currentItem];
  img.src = item.img;
  firstName.textContent = item.name;
  review.textContent = item.text;
}

// Show next person
nextBtn.addEventListener('click', function () {
  currentItem++;
  if (currentItem > testimonials.length - 1) {
    currentItem = 0;
  }
  showPerson();
});

// Show prev person
prevBtn.addEventListener('click', function () {
  currentItem--;
  if (currentItem < 0) {
    currentItem = testimonials.length - 1;
  }
  showPerson();
});